nav { 
    position: absolute;
    z-index: 2;    
    width: 70%;
    font-family: montserrat, sans-serif;
    backdrop-filter: blur(10px);
    background-color: transparent;
    color:#636d6c;

}

ul {
    display: flex;
    justify-content: space-between;
    list-style: none;
    width: 400px;
    padding-left: 0!important;
}

li { 
    flex: 1;
    color:#636d6c;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

li.active { 
    color: #8ed1d1;

}
li:first-child.active {
    border-radius: 20px 0px 0px 20px;
  }
  
li:last-child.active  {
    border-radius: 0px 20px 20px 0px;

}

a { 
    outline: none;
    color:white;
    text-decoration: none;
    width: 100%;
    font-size: 1rem;
    text-align: center;
    opacity: 1 !important;
    z-index: 5;
}


.menu-icon {
    height: 4em;
    width: 4em;
    background-color: rgb(0,0,0,0.7);
    top: 0; /* Adjust as needed */
    right: 3em; /* Adjust as needed */
    float: right;
    position: absolute;
    z-index: 2;
    top: 40%;
    border-radius: 5em;
    padding: 0;
    text-align: center;
    line-height: 3em;
    display: none;
}   

.nav-container { 
    width: 100%;
    height: 90px;
    position: fixed;
    top: 0;
    z-index: 2;
}

