.intro-page { 
    width: 100%;
    height: 100vh;
    margin: 0;
    padding: 0;
    position: relative;
    display: flex;
    align-items: center; /* Center vertically */
    justify-content: center; /* Center horizontally */
}

.main-text{ 
    z-index: 1;
    position: absolute;
    color: white;
    text-align: center;
}

.main-text h1 { 
    font-size: 5vw;
    margin-bottom: 20px;
}

.main-text h2 { 
    font-size: 2vw;
}

.coloured-text { 
    color:#8ed1d1;
}

