.swiper {
    width: 100%;
    height: 400px!important;
    overflow: hidden;

}

.ReactFlipCard__container {
    width: 100% !important;
    height: 100% !important;
}

.ReactFlipCard__flipCard {
    position: absolute;

}

.ReactFlipCard__flipCardBack {
    position: absolute;
}

.swiper-wrapper {
    height: 350px !important;
}

.project-page { 
    width: 100%;
    min-height: 400px;
    position: relative;
    display: flex;    
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    font-family: montserrat, sans-serif;
}

.project-title {
    font-size: 3rem;
    color: #8ed1d1;
    position: relative;
    margin-bottom: 1em;
    margin-top: 1em;
}

.project-paragraph { 
    max-width: 1100px;
    font-size: 1.2rem;
    line-height: 1.7em;
    letter-spacing: 0.1em; 
    color: #636d6c;
    font-weight: 500;
}

.project-text {
}

.project-box {
    position: relative;
    margin-top: 1em;
    background-color: #fffaf7;
    width: 70%;
    border-radius: 40px;
    padding: 5em;
    display: flex; 
    flex-direction: column;
    align-content: flex-start;
    align-items: center;
    box-shadow: 5px 10px 10px #38383f;
}

.project-collection { 
    padding-top: 30px;
    display: flex;
    position: relative;
    gap: 2em;
    top: 2em;
    flex-wrap: wrap;
    flex-direction: column;

}

.project-card { 
    display: flex;
    flex-direction: column;
    background-color: #dddddd;
    border-radius: 10px;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
    width: 300px;
    height: 100%;
    position: relative;
}

.project-card-inner {
    width: 100%;
    height: 100% !important;
    height: fit-content;
}
.project-card img { 
    width: 100%;
    height: 10em;
    border-radius: 6px;
}

.project-card h2 { 
    padding-left: 1rem;
    padding-right: 1em;
    color: #636d6c;
    margin-bottom: 1em;
    font-size: 1rem;
    font-weight: 900;
    letter-spacing: 0.05rem;
    max-height: 50px;
    overflow: hidden;
}
.project-card p { 
    padding-left: 1em;
    padding-right: 1em;
    color: #636d6c;
    margin-bottom: 1em;
    font-size: 1rem;
}
.tag-collection { 
    display: flex;
    gap: 1em;
    margin-top: 1.5em;
    margin-bottom: 1.5em;
    padding-left: 1em;
    padding-right: 1em;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
}

.tag { 
    background-color: #8ed1d1;
    width: 5.5em;
    height: 2em;
    text-align: center;
    color: rgb(20, 33, 104);
    border-radius: 5px;
    font-size: 0.8rem;
    line-height: 2em;
    font-weight: 700;
}

.github-button {
    background-color: 	#fddae2;
    color: rgb(20, 33, 104);
    width: 100%;
    height: 2em;
    font-weight: 700;
    border-radius: 5px;
    position: absolute;
    padding: 0;
    display: flex;
    border: 0;
    bottom: 0;
}


.github-button:hover {
    background-color: 	#e8c7cf;
}

.github-button a { 
    color: rgb(20, 33, 104);

    height: 100%;
    width: 100%;
    margin: 0;
    position: absolute;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
}

.github-button i { 
    margin-right: 0.5em;
}

@media (max-width: 500px) {
    .project-card img { 
        width: 100%;
        height: 25em;
        border-radius: 6px;
        object-position: center;

    }
    
}