.skills-page { 
    width: 100%;
    position: relative;
    display: flex;    
    align-items: center;
    flex-direction: column;
    align-content: flex-start;
    font-family: montserrat, sans-serif;
}

.skills-box {
    position: relative;
    margin-top: 1em;
    background-color: 	#fffaf7;
    width: 70%;
    border-radius: 40px;
    padding: 5em;
    display: flex; 
    flex-direction: column;
    align-content: flex-start;
    align-items: center;
    box-shadow: 5px 10px 10px #38383f;

}

.skills-title { 
    margin-top: 1em;
    margin-bottom: 1em;
    font-size: 3em;
    color: #8ed1d1;
    position: relative;
}

.skills-paragraph { 
    max-width: 1100px;
    color: rgb(255, 255, 255);
    font-size: 1.2rem;
    line-height: 1.7em;
    letter-spacing: 0.1em; 
    color: #636d6c;
    font-weight: 500;
}

.proglang { 
    color: 	#8ed1d1;
}

.skills-collection { 
    display: flex;
    flex-direction: column;
    align-content: space-evenly;
    align-items: center;
    position: relative;
    gap: 2em;
    top: 2em;
    align-items: stretch;

}

.skills-card { 
    width: 100%;
    text-align: center;
    background-color: #dddddd;
    border-radius: 10px;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
    display: flex;    
    flex-direction: column;
    align-content: flex-start;
    flex: 1;
}

.skills-card h2 { 
    font-size: 1.5em;
    color: #636d6c;
    position: relative;
    top: 1.5em;
}

.skills-icons { 
    display: flex;    
    align-items: stretch;
    position: relative;
    flex-wrap: wrap;
    margin-top: 0.5em;
    gap: 1em;
    color: white;
    justify-content: space-between;
    flex: 1;
    padding: 1em;
    font-size: 5em;
}

@media screen and (max-width: 500px) {
    .skills-collection {
        flex-direction: column;
    
    }
}